import { Box, Stack } from "@mui/material";
import React, { useState } from "react";
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

const styleUs = { position: "absolute", height: "100%", maxHeight: "300px", alignContent: "center", ':hover': { backgroundColor: "#ffffff30", color: "#ebecf0" } }

const PhotosShower = ({ photoArr }) => {
    const [indexCurrentPhoto, setIndexCurrentPhoto] = useState(0);
    const len = photoArr.length;
    function nextPhoto() {
        if (indexCurrentPhoto + 1 <= len - 1) {
            setIndexCurrentPhoto(indexCurrentPhoto + 1);
            return;
        }
        setIndexCurrentPhoto(0);
    }
    function prevPhoto() {
        if (indexCurrentPhoto - 1 >= 0) {
            setIndexCurrentPhoto(indexCurrentPhoto - 1);
            return;
        }
        setIndexCurrentPhoto(len - 1);
    }

    return (
        <Stack direction={"row"}
            sx={{
                position: "relative",
                maxHeight: "300px",
                maxWidth: "500px",
                overflow: "hidden",
                borderRadius: "16px",
            }}>
            <Box onClick={prevPhoto} sx={styleUs}>
                <ArrowBackIosNewIcon sx={{ marginLeft: "16px", marginRight: "48px" }} />
            </Box>
            {/* !!! сделать гибкость под горизонтальные фото */}
            <img src={photoArr[indexCurrentPhoto]} alt={photoArr[indexCurrentPhoto]}
                style={{ width: "100%", height: "auto" }}
            />
            <Box onClick={nextPhoto} sx={{ ...styleUs, marginLeft: "82%" }}>
                <ArrowForwardIosIcon sx={{ marginRight: "16px", marginLeft: "48px" }} />
            </Box>
        </Stack>
    )
}

export default PhotosShower;